<template>
  <div class="box">
    <!-- 我的余额 -->
    <div v-show="!flag">
      <!-- 表头 -->
      <van-sticky>
        <van-nav-bar
          title="我的余额"
          left-text=""
          left-arrow
          @click-left="zqd_go()"
        >
          <template #right>
            <span class="zqd_sp" @click="flag = true">余额明细</span>
          </template>
        </van-nav-bar>
      </van-sticky>
      <div class="con">
        <!-- 我的0余额 -->
        <p class="zqd_p">
          <span class="zqd_sp2">我的余额</span>
          <span class="zqd_sp3">{{ list.coinUnitType }} 学习币</span>
        </p>
        <!-- 充值列表 -->
        <ul class="zqd_ul">
          <li
            v-for="(item, index) in list.list"
            :key="index"
            @click="dian(item.id)"
          >
            <p>{{ item.coin }}学习币</p>
            <p class="zqd_p2">￥{{ item.rmb.toFixed(2) }}</p>
          </li>
        </ul>
        <!-- 充值说明 -->
        <p class="zqd_pp">充值说明</p>
        <div class="explain" style="color: #333">
          {{ list.coinInfo.agreement }}
        </div>
      </div>
      <button
        id="zqd"
        :disabled="disabled"
        @click="chong"
        :style="disabled ? 'backgroundColor:#ccc' : ''"
      >
        立即充值
      </button>
      <van-popup v-model="show" position="bottom" :style="{ height: '30%' }">
        <p class="title">支付方式</p>
        <div>
          <p>
            <input type="radio" name="1" v-model="asd" value="wx" />
            <img
              src="data:image/png;base64,"
              alt=""
            />
            <span>微信</span>
          </p>
          <p>
            <input type="radio" name="1" v-model="asd" value="ali" />
            <img
              src="data:image/png;base64,"
              alt=""
            />
            <span>支付宝</span>
          </p>
        </div>
      </van-popup>
    </div>
    <div v-show="flag">
      <van-sticky>
        <van-nav-bar
          title="余额明细"
          left-text=""
          left-arrow
          @click-left="onClickLeft"
        />
      </van-sticky>
      <div class="cxy-box" v-for="(item, index) in list1" :key="index">
        <p>
          {{ item.shop_name }} <span>{{ item.change_price }}</span>
        </p>
        <p>
          {{ item.remaining_sum }}
          <span>{{ item.created_at | timetesev }}</span>
        </p>
        <p></p>
      </div>
      <p class="p23">没有更多了</p>
    </div>
  </div>
</template>

<script>
import { gets, posts } from "../util/api";
export default {
  data() {
    return {
      list: [],
      flag: false,
      list1: [],
      disabled: true,
      show: false,
      coin_id: "",
      asd: "",
    };
  },
  created() {},
  async mounted() {
    let { data } = await gets("coin/coinRank");
    console.log(data);
    this.list = data.data;
    let { data: res } = await gets("coin/item", {
      page: 1,
    });
    console.log(res);
    this.list1 = res.data.list;
  },
  methods: {
    zqd_go() {
      this.$router.go(-1);
    },
    onClickLeft() {
      this.$router.go(-1);
    },
    dian(id) {
      console.log(event.target);
      [...event.target.parentNode.childNodes].forEach((item) => {
        item.classList.remove("active");
      });
      event.target.classList.add("active");
      this.disabled = false;
      this.coin_id = id;
    },
    async chong() {
      if (this.show == false) {
        this.show = true;
      } else {
        let { data } = await posts("pay", {
          coin_id: this.coin_id,
          type: this.asd,
        });
        console.log(data);
      }
    },
  },
};
</script>

<style scoped lang="scss">
.van-popup {
  > div {
    > p {
      height: 0.8rem;
      line-height: 0.8rem;
    }
    input {
      margin: 0 0.2rem;
    }
    img {
      width: 0.46rem;
      height: 0.46rem;
    }
  }
}
.title {
  position: relative;
  padding-left: 0.14667rem;
  padding-left: 1.46667vw;
  padding: 0 0.33333rem 0 0.54667rem;
  padding: 0 3.33333vw 0 5.46667vw;
  font-size: 0.4rem;
  font-size: 4vw;
  font-family: PingFang SC;
  font-weight: 700;
  color: #333;
  line-height: 1.2rem;
  line-height: 12vw;
}
.title:before {
  content: "";
  position: absolute;
  left: 0;
  top: calc(50% - 0.10667rem);
  top: calc(50% - 1.06667vw);
  width: 0.04rem;
  width: 0.4vw;
  height: 0.21333rem;
  height: 2.13333vw;
  background: #dc692a;
  border-radius: 0.02667rem;
  border-radius: 0.26667vw;
}
.cxy-box {
  padding: 0.2rem 0.4rem;
  p {
    font-size: 0.3rem;
    color: #333333;
    display: flex;
    justify-content: space-between;
    margin-top: 0.2rem;
  }
  :nth-child(2) {
    font-size: 0.24rem;
    color: #999999;
  }
}

.p23 {
  text-align: center;
  font-size: 0.24rem;
  color: #969799;
}
.zqd_sp {
  font-size: 0.12rem;
}

.zqd_p {
  box-sizing: border-box;
  padding: 0.2rem 0;
  line-height: 1rem;
  font-family: PingFang SC;
  font-weight: 500;
}

.zqd_p span {
  margin-left: 0.17rem;
  font-size: 4vw;
  line-height: 15.33333vw;
}

.zqd_p .zqd_sp3 {
  color: #dc692a;
}

.zqd_ul {
  box-sizing: border-box;
  padding: 0.12rem 0.1rem 0.3rem 0.12rem;
  background: #eee;
}
.zqd_ul {
  li {
    box-sizing: border-box;
    padding: 0 0.12rem;
    display: flex;
    align-items: center;
    justify-content: space-between;
    box-sizing: border-box;
    padding: 0 4.8vw;
    height: 1rem;
    background: #fff;
    border-radius: 0.18rem;
    margin-top: 0.5rem;
    p{
       font-size: 3.73333vw;
    }
  }
  > .active {
    background: #fcede2;
    border: 0.26667vw solid #eb6100;
    box-shadow: 0 0.66667vw 0.66667vw 0 rgba(235, 97, 0, 0.75);
    border-radius: 2.13333vw;
  }
}

.zqd_ul li .zqd_p2 {
  color: #e90000;
}

#zqd {
  width: 100%;
  height: 13.06667vw;
  margin: 0;
  padding: 0;
  left: 0;
  border: 0;
  position: fixed;
  bottom: 0;
  background-color: #eb6100;
  font-size: 4.26667vw;
  font-family: PingFang SC;
  font-weight: 400;
  color: #fff;
  z-index: 2100;
  text-align: center;
}

.zqd_pp {
  position: relative;
  padding-left: 1.46667vw;
  padding: 0 3.33333vw 0 5.46667vw;
  font-size: 4vw;
  font-family: PingFang SC;
  font-weight: 700;
  color: #333;
  line-height: 12vw;
  background-color: #fff;
  margin-top: 0.2rem;
}
.zqd_pp::before {
  content: "";
  position: absolute;
  left: 0;
  top: calc(50% - 1.06667vw);
  width: 0.4vw;
  height: 2.13333vw;
  background: #dc692a;
  border-radius: 0.26667vw;
}
.explain {
  position: relative;
  padding-left: 5.33333vw;
  font-size: 3.2vw;
  font-family: PingFang SC;
  font-weight: 300;
  color: #666;
  line-height: 4.8vw;
  margin-bottom: 1rem;
}
</style>